import style from "./OngoingAdmission.module.css";
import { assestsUserAdmissionUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { Link, useLocation, useParams } from "react-router-dom";

const OngoingAdmissionTopHostelCard = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();

  // console.info(
  //   "admissionMergedApplicationTotalCount",
  //   admissionMergedApplicationTotalCount
  // );
  return (
    <>
      <Link
        to={
          getQuery.state?.urlOffSet === "Institute"
            ? `/q/${params.username}/institute/member/admission?type=hostel-ongoing`
            : `/q/${params.username}/member/staff/admission?type=hostel-ongoing`
        }
        state={getQuery.state}
      >
        <div
          className={style.ongoing_card}
          style={{
            position: "relative",
            boxShadow: "0 4px 4px 1px rgba(0, 0, 0, 0.4)",
          }}
        >
          <div className={style.ongoing_card_inner}>
            <img
              alt="Ongoing App"
              src={`${assestsUserAdmissionUrl}/application.svg`}
            />
            <div className={style.ongoing_card_text}>
              <h6>{t("hostel_application")}</h6>
            </div>
          </div>
        </div>
      </Link>
    </>
  );
};

export default OngoingAdmissionTopHostelCard;
